import React from 'react';
import { Link } from 'react-router';
import 'whatwg-fetch';

class Home extends React.Component {
	constructor(){
		super();

		this.getUser = this.getUser.bind(this);
	}

	getUser(){

		fetch('/api/user', { method: 'GET' })
			.then((res) => res.json())
			.then((data) => {
				console.log(data);
			})
			.catch((e) => console.log('Parsing failed:', e));

	}
	render() {
		return (
			<span>
			<div style={{backgroundColor:'#c9c9c9', padding:'20px'}}>
				<Link to="route1">Route 1</Link>
				{' '}
				<Link to="route2">Route 2s</Link>
			</div>
			<div onClick={this.getUser}>Get User</div>
			{this.props.children}
			</span>
		)
	}
}

export default Home;


