import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, browserHistory } from 'react-router';
import App from './App/App';
import Route1 from './Route1/Route1';
import Route2 from './Route2/Route2';

// Configure "Plain" Routes
// Fixes HMR warning
// https://github.com/reactjs/react-router/blob/master/docs/guides/RouteConfiguration.md#configuration-with-plain-routes

const routes = {
	path: '/',
	component: App,
	childRoutes: [
		{
			path: '/route1',
			component: Route1
		},
		{
			path: '/route2',
			component: Route2,
		}
	]
}

class Root extends React.Component {
	render() {
		return (
			<Router history={browserHistory} routes={routes}></Router>
		)
	}
}

ReactDOM.render(<Root />, document.getElementById('root'));


